import React, { useEffect, useState } from "react";
import MapComponent from "./maps/MapComponent";
import logo from "./logo.svg";
import "./App.css";
import FileInput from "./components/FileInput";
// import demoData from "./data/demoData.json";
import coronaData from "./data/coronaData.json";

var rad = function (x) {
  return (x * Math.PI) / 180;
};

var getDistance = function (p1, p2) {
  var R = 6378137; // Earthâ€™s mean radius in meter
  var dLat = rad(p2.lat - p1.lat);
  var dLong = rad(p2.lng - p1.lng);
  var a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(rad(p1.lat)) *
    Math.cos(rad(p2.lat)) *
    Math.sin(dLong / 2) *
    Math.sin(dLong / 2);
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var d = R * c;
  return d; // returns the distance in meter
};


//comment something, and another something...
const getPosition = startLocation => {
  const { latitudeE7, longitudeE7 } = startLocation;
  const fixedLatitudeE7 =
    latitudeE7 > 900000000 ? latitudeE7 - 4294967296 : latitudeE7;
  const fixedLongitudeE7 =
    longitudeE7 > 1800000000 ? longitudeE7 - 4294967296 : longitudeE7;
  const lat = fixedLatitudeE7 / 10e6;
  const lng = fixedLongitudeE7 / 10e6;
  return { lat, lng };
};

const getTs = dateStr => {
  const date = new Date(dateStr);
  return date.getTime();
};
const calculateCorona = () => {
  const result = [];
  coronaData.forEach(corona => {
    result.push({
      lat: corona.lat,
      lng: corona.lon,
      startTimestampMs: getTs(corona.t_start),
      endTimestampMs: getTs(corona.t_end)
    });
  });
  return result;
};

const calculateUser = (userHistory) => {
  const { timelineObjects } = userHistory;

  const walkingOrRunning = timelineObjects.filter(item => {
    if (item.activitySegment) {
      const { activityType } = item.activitySegment;

      if (
        activityType === "WALKING" ||
        activityType === "RUNNING" ||
        activityType === "STILL"
      ) {
        return true;
      } else {
        return false;
      }
    }
  });

  const result = [];

  walkingOrRunning.forEach(activity => {
    const { activitySegment } = activity;
    const { startLocation, endLocation, duration } = activitySegment;
    result.push({ ...getPosition(startLocation), ...duration });
    result.push({ ...getPosition(endLocation), ...duration });
  });

  return result;
};

const calcLast2Weeks = locations => {
  console.log(`calcLast2Weeks start`);
  const runningDate = new Date();
  let counter = 14;
  const result = {};
  while (counter > 0) {
    runningDate.setDate(runningDate.getDate() - counter);
    result[runningDate.getTime()] = null;
    counter--;
  }

  Object.keys(result).forEach(dayTimeStamp => {
    const curreDateObj = new Date(Number(dayTimeStamp));
    const currDate = curreDateObj.getDate();
    locations.forEach(location => {
      const startDate = new Date(Number(location.startTimestampMs));
      const endDate = new Date(Number(location.endTimestampMs));
      if (startDate.getDate() === currDate || endDate.getDate() === currDate) {
        result[dayTimeStamp] = location;
      }
    });
  });

  return result;
};

const calcUserAndCorona = (last2weeksUserLocations, coronas) => {
  const result = [];
  Object.keys(last2weeksUserLocations).forEach(dayTimeStamp => {
    if (last2weeksUserLocations[dayTimeStamp] !== null) {
      const curreDateObj = new Date(Number(dayTimeStamp));
      const currDate = curreDateObj.getDate();
      coronas.forEach(corona => {
        const coronaStart = new Date(Number(corona.startTimestampMs));
        const coronaEnd = new Date(Number(corona.endTimestampMs));

        if (
          coronaStart.getDate() === currDate ||
          coronaEnd.getDate() === currDate
        ) {
          result.push({ user: last2weeksUserLocations[dayTimeStamp], corona });
        }
      });
    }
  });

  return result;
};

const calcDangerUserCorona = userAndCoronas => {
  const result = [];
  userAndCoronas.forEach(dayUserCorons => {
    const { user, corona } = dayUserCorons;
    const distance = getDistance(
      { lat: user.lat, lng: user.lng },
      { lat: corona.lat, lng: corona.lng }
    );
    if (distance <= 200) {
      result.push({ ...dayUserCorons, distance });
    }
  });

  return result;
};

function App() {
  const [userPositions, setUserPositions] = useState([]);
  const [coronaPositions, setCoronaPositions] = useState([]);
  const [userHistory, setUserHistory] = useState([]);
  useEffect(() => { }, []);
  return (
    <div className="App">
      <div className="App-header">
        <FileInput setUserHistory={setUserHistory} />
        <div
          className={"calc"}
          onClick={() => {
            console.log(`userHistory keys are ${Object.keys(userHistory)}`);
            const locations = calculateUser(userHistory);
            const last2weeksUserLocations = calcLast2Weeks(locations);
            const coronas = calculateCorona();
            const userAndCoronas = calcUserAndCorona(
              last2weeksUserLocations,
              coronas
            );

            const dangerUserAndCorons = calcDangerUserCorona(userAndCoronas);
            // console.log(
            //   `****DANGER**** ${JSON.stringify(dangerUserAndCorons)}`
            // );

            const userPos = dangerUserAndCorons.map(userAndCoron => {
              return {
                lat: userAndCoron.user.lat,
                lng: userAndCoron.user.lng,
                startTimestampMs: userAndCoron.user.startTimestampMs,
                endTimestampMs: userAndCoron.user.endTimestampMs,
                distance: userAndCoron.distance
              };
            });

            console.log(`userPos ${JSON.stringify(userPos)}`);

            setUserPositions(userPos);

            const coronaPos = dangerUserAndCorons.map(userAndCoron => {
              return {
                lat: userAndCoron.corona.lat,
                lng: userAndCoron.corona.lng,
                startTimestampMs: userAndCoron.corona.startTimestampMs,
                endTimestampMs: userAndCoron.corona.endTimestampMs,
                distance: userAndCoron.distance
              };
            });
            console.log(`coronaPos ${JSON.stringify(coronaPos)}`);
            setCoronaPositions(coronaPos);
          }}
        >
          Calculate
        </div>
      </div>

      <MapComponent
        userPositions={userPositions}
        coronaPositions={coronaPositions}
      />
    </div>
  );
}

export default App;
