import { Map, InfoWindow, Marker, GoogleApiWrapper } from "google-maps-react";

import React, { useEffect, useState } from "react";

import "./styles.scss";

const getTime = (startTimestampMs) => {
  const thisTime = new Date(Number(startTimestampMs));
  return thisTime;
}

const MapContainer = props => {
  const [mapCenter, setMapCenter] = useState({
    latitude: 32.0798809,
    longitude: 34.7855687
  });

  const [activeMarker, setActiveMarker] = useState({ marker: null, text: null });

  useEffect(() => {
    if (!navigator.geolocation) {
      alert("Geolocation is not supported by your browser");
    } else {
      const success = position => {
        const { latitude, longitude } = position.coords;
        setMapCenter({ latitude, longitude });
      };

      const error = err => {
        console.error(`Error google maps ${err}`);
      };
      navigator.geolocation.getCurrentPosition(success, error);
    }
  }, []);
  return (
    <Map
      google={props.google}
      zoom={10}
      initialCenter={{
        lat: 32.0798809,
        lng: 34.7855687
      }}
    >
      {props.userPositions.map(currMarker => (
        <Marker
          key={currMarker.lat + "_" + currMarker.lng}
          title={`${currMarker.startTimestampMs}`}
          name={"SOMA"}
          position={{ lat: currMarker.lat, lng: currMarker.lng }}
          onClick={(props, marker, e) => setActiveMarker({ marker, text: `User was here at ${getTime(currMarker.startTimestampMs)} distance: ${parseInt(currMarker.distance)} meters` })}
        />
      ))}

      {props.coronaPositions.map((currMarker, i) => (
        <Marker
          key={currMarker.lat + "_" + currMarker.lng + "_" + i}
          title={"ori"}
          name={"SOMA"}
          position={{ lat: currMarker.lat, lng: currMarker.lng }}
          icon={{
            url: "http://maps.google.com/mapfiles/ms/icons/blue-dot.png"
          }}
          onClick={(props, marker, e) => setActiveMarker({ marker, text: `corona was here at ${getTime(currMarker.startTimestampMs)} distance: ${parseInt(currMarker.distance)} meters` })}
        />
      ))}

      <InfoWindow
        visible={activeMarker.marker !== null}
        style={{ backgroundColoe: "#fff" }}
        marker={activeMarker.marker}
      >
        <div className={"infoWindow"}>
          <p>
            {activeMarker.text}
          </p>
        </div>
      </InfoWindow>
    </Map>
  );
};
class MapContainerClass extends React.Component {
  render() {
    return <MapContainer {...this.props} />;
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyCb5MztDrAyVmNx8GC4kFLLJqWqZkt0teA"
})(MapContainerClass);
