import React, { useRef } from "react";
var unzip = require("unzip-js");

const FileInput = props => {
  const inputEl = useRef(null);

  const handleSubmit = event => {
    event.preventDefault();
    console.log(`handleSubmit clicked`);
    if (inputEl.current.files[0]) {
      console.log(`Selected file - ${inputEl.current.files[0].name}`);
      console.log(`Selected size - ${inputEl.current.files[0].size}`);

      unzip(inputEl.current.files[0], (err, zipFile) => {
        if (err) {
          return console.error(err);
        }

        zipFile.readEntries((err, entries) => {
          if (err) {
            return console.error(err);
          }

          entries.forEach(function (entry) {
            // console.log(`reading data for ${entry.name}`);
            if (entry.name.indexOf("2020_MARCH.json") > -1) {
              zipFile.readEntryData(entry, false, function (err, readStream) {
                if (err) {
                  return console.error(err);
                }

                var data = "";

                readStream.on("data", function (chunk) {
                  data += chunk;
                });
                readStream.on("error", function (err) {
                  console.log(`error`);
                });
                readStream.on("end", function () {
                  const dataJson = JSON.parse(data);

                  props.setUserHistory(dataJson);
                });
              });
            }
          });
        });
      });
    }
  };

  return (
    <div>
      <label>
        Upload file:
        <input type="file" ref={inputEl} />
      </label>
      <br />
      <button onClick={handleSubmit}>Submit</button>
    </div>
  );
};
export default FileInput;
